@import url('https://fonts.googleapis.com/css2?family=Abel&display=swap');

.searchbar{
    width: 25%;
    height: auto;
    margin: 0 auto;
    text-align: center;
}

.searchbar input{
    margin:  30px 0;
    padding: 5px 10px;
    border-radius: 5px;
    font-family: 'Abel', sans-serif;
    border: 1px solid black;
    color: gray;
}
footer{
    /* position: fixed; */
  
  width: 100%;
  font-family: 'Montserrat', sans-serif;
  font-size: 19px;
    
  
  color: grey;
  text-align: center;

    
}
.img img{
    margin-left: 26%;    
    justify-content: center;
}
.img{
    justify-content: center;
}

@media (max-width:768px){
    .searchbar{
        width: 50%;

    }
    .searchbar input{
       
        font-size: 19px;
    }
    .img img{
        width: 100%;
        height: 430px;
        margin-left: 0px;
    }
}